@import 'config';
@import 'utilities';

// General Styles
* {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Dosis', sans-serif;
    line-height: 1.6;
    background: #fff;
}

a {
    text-decoration: none;
    color: $dark-color;
}

ul {
    list-style: none;
}

h2, h3, h4 {
    text-transform: uppercase;
}

img {
    width: 100%;
}

// logo
#logo {
    width: 70px;
    height: 70px;
    color: #fff;
    text-transform: uppercase;
}

//Main Nav
#main-nav {
    display: flex;
    justify-content: space-between;
    padding-top: 1rem;

    ul {
        display: flex;
    }

    li {
        padding: 1rem 1.5rem;
    }

    a {
        text-decoration: none;
        color: #fff;
        text-transform: uppercase;
        border-bottom: 3px transparent solid;
        padding-bottom: 0.5rem;
        transition: border-color 0.5s;

        &:hover {
            border-color: $medium-color;
        }

        &.current {
            border-color: $main-color;
        }
    }
}
// Header
#header {
    &-home {
        background: $bg-image no-repeat center right / cover;
        height: 100vh;
        color: #fff;

        // Header Content 
        .header-content {
            text-align: center;
            padding-top: 15%;

            h1 {
                font-size: 4rem;
                line-height: 1.2;
            }
        }
    }

    &-inner {
        background: $bg-image no-repeat 20% 30% / cover;
        height: 5.5rem;
        border-bottom: 3px solid $main-color;
        color: #fff;
    }
}

// Home Sections

#home {

    &-a {
        .specials {
            margin-top: 3rem;
            display: grid;
            grid-gap: 1rem;
            grid-template-columns: repeat(4, 1fr);

            .fas {
                color: $main-color;
                padding-bottom: 0.4rem;
            }
        }
    }

    &-b {
        .stats {
            display: grid;
            grid-template-columns: repeat(4, 1fr);

            li {
                line-height: 2;

                &.stats-title {
                    font-size: 1.5rem;
                }

                &.stats-number {
                    font-size: 2rem;
                    font-weight: bold;
                }
            }

            div {
                padding: 3rem;
                 &:nth-child(odd) {
                    background: $light-color;
                 }

                 &:nth-child(even) {
                    background: $medium-color;
                }
            }
        }
    }

    &-c {
        .process {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            grid-gap: 1.5rem;
            text-align: center;

            // process-step 
            &-step {
                position: absolute;
                top: 0;
                right: 0;
                font-size: 28px;
                background-color: $main-color;
                color: set-text-color($main-color);
                border-radius: 50%;
                height: 15px;
                width: 15px;
                line-height: 15px;
                padding: 1rem;
                transition: all 1s;
            }
            
            // process-icon
            &-icon {
                border-radius: 50%;
                background: $dark-color;
                color: set-text-color($dark-color);
                padding: 2rem;
                height: 70px;
                width: 70px;
                line-height: 70px;
                text-align: center;
                position: relative;
                transition: all 1s;

                &:hover {
                    background: $main-color;
                    height: 90px;
                    width: 90px;
                    line-height: 90px;
                    cursor: pointer;

                    .process-step {
                        background: $dark-color;
                        color: set-text-color($dark-color);
                    }
                }
            }
        }
    }
}

// About Sections
#about {

    &-a {
        .about-info {
            display: grid;
            grid-template-areas:
            'bio-image bio bio'
            'award-1 award-2 award-3';
            grid-gap: 1.2rem;
        }

        // Grid Areas
        .bio-image {
            grid-area: bio-image;
        }

        .bio {
            grid-area: bio;
            border-left: 3px solid $main-color;
            padding: 1rem;
            
            
        }

        .award {

            &-1 {
                grid-area: award-1;

                .fas {
                    color: $main-color;
                    margin: 1rem;
                }
            }

            &-2 {
                grid-area: award-2;
                .fas {
                    color: $main-color;
                    margin: 1rem;
                }
            }

            &-3 {
                grid-area: award-3;
                .fas {
                    color: $main-color;
                    margin: 1rem;
                }
            }
        }
    }

    // Section B: Progress Bars
    &-b {
        .progress {
            overflow: hidden;
            height: 20px;
            background: $medium-color;
            border-radius: 5px;
            margin-bottom: 0.6rem;

            div {
                height: 100%;
                color: #fff;
                text-align: center;
                background: $main-color;
            }
        }
    }

    // Section C: Logos
    &-c {
        .about-logos {
            display:grid;
            grid-template-columns: repeat(4, 1fr);
            grid-gap: 2rem;
            justify-content: center;

            img {
                width: 80%;
            }
        }
    }

    // Section D: Testimonials
    &-d {
        .testimonials {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            grid-gap: 0.5rem;

            ul {
                list-style: none;
                margin-top: 1rem;
                display: flex;
                align-items: center;
            }

            p {
                border: 1px solid $medium-color;
                border-radius: 5px;
                padding: 0.5rem;
            }

            img {
                width: 50px;
                height: 50px;
                border-radius: 50%;
                margin-right: 0.5rem;
            }
        }
    }  
}

// Contact Page
#contact {

    &-a {

        .text-fields {
            display: grid;
            grid-template-areas:
            'name email'
            'subject phone'
            'message message';
            grid-gap: 1.2rem;
            margin-bottom: 1.2rem;

            //. Grid Areas
            .name-input {
                grid-area: name;
            }

            .email-input {
                grid-area: email;
            }
            .subject-input {
                grid-area: subject;
            }
            .phone-input {
                grid-area: phone;
            }
            .message-input {
                grid-area: message;
                height: 100px;
            }
            .text-input {
                padding: 0.5rem 1rem;
            }
        }

        // Button
        button[type="Submit"] {
            width: 50%;
            color: #fff;
        }       
    }

    &-b {
        .contact-info {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            text-align: center;
        }
    }

    &-c {
        h1 {
            text-align: center;
            font-size: 3rem;
        }
    }
}

#main-footer {
    text-align: center;
    background: $dark-color;
    height: 5rem;
    color: #fff;

    .footer-content {
        display: flex;
        justify-content: space-between;
        height: 5rem;
        align-items: center;

        .social .fab {
            margin-right: 1rem;
            border: 2px white solid;
            border-radius: 50%;
            height: 20px;
            width: 20px;
            line-height: 20px;
            text-align: center;
            padding: 0.5rem;

            &:hover {
                background: $main-color;
                cursor: pointer;
            }
        }
    }
}

@import 'media';